            </el-table-column>
            <el-table-column label="指标分类" header-align="center" align="center">
                <template slot-scope="scope"><!-- 1.基础指标 2.分析指标 -->
                    <span>{{scope.row.indiType===1?'基础指标':'分析指标'}}</span>
                </template>
            </el-table-column>
            <el-table-column label="计算规则" header-align="center" align="center">
                <template slot-scope="scope">
                    <span>{{scope.row.rule}}</span>
                </template>
            </el-table-column>
            <el-table-column label="所属机构" header-align="center" align="center">
                <template slot-scope="scope">
                    <span>{{scope.row.instName}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="version" label="版本" header-align="center" align="center"></el-table-column>
            <el-table-column prop="comment" label="备注" header-align="center" align="center"></el-table-column>
            <el-table-column label="创建人" header-align="center" align="center">
                <template slot-scope="scope">
                    <span>{{scope.row.createdBy || '管理员' }}</span>
                </template>
            </el-table-column>
            <el-table-column label="创建时间" header-align="center" align="center" width="150">
                <template slot-scope="scope">
                    <span>{{new Date(scope.row.createDt) | convertDate('yyyy-MM-dd hh:mm')}}</span>
                </template>
            </el-table-column>
            <el-table-column label="最后修改人" header-align="center" align="center">
                <template slot-scope="scope">
                    <span>{{scope.row.lastEditBy || '管理员' }}</span>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
            v-if="quotaTotal > 0"
            class="text-right m-t-20"
            layout="prev, pager, next"
            :current-page.sync="quotaPage"
            :page-size="pageSize"
            :total="quotaTotal"
            @current-change="getQuotaDatas"
        >
        </el-pagination>

        <!-- 添加/编辑 指标 弹窗start -->
        <el-dialog
            :title="addQuotaTitle"
            :visible.sync="addQuotaVisible"
            width="600px"
        >
            <el-form :model="quotaForm">
                <el-form-item label="一级指标类别" :label-width="formLabelWidth" required>
                    <span v-if="quotaForm.id">{{quotaForm.classify1Name}}</span>
                    <el-select class="search-selector m-r-20" style="width:200px;" clearable v-model="quotaForm.classify1" placeholder="请选择一级指标类别" v-else>
                        <el-option v-for="item in aLevel1" :key="item.id" :label="item.name" :value="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="二级指标类别" :label-width="formLabelWidth" required>
                    <span v-if="quotaForm.id">{{quotaForm.classify2Name}}</span>
                    <el-select class="search-selector m-r-20" style="width:200px;" clearable v-model="quotaForm.classify2" placeholder="请选择二级指标类别" v-else>
                        <el-option v-for="item in aaLevel2" :key="item.id" :label="item.name" :value="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="三级指标类别" :label-width="formLabelWidth" required>
                    <span v-if="quotaForm.id">{{quotaForm.classify3Name}}</span>
                    <el-select class="search-selector m-r-20" style="width:200px;" clearable v-model="quotaForm.classify3" placeholder="请选择三级指标类别" v-else>
                        <el-option v-for="item in aaLevel3" :key="item.id" :label="item.name" :value="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="指标项" :label-width="formLabelWidth" required>
                    <span v-if="quotaForm.id">{{quotaForm.indiModelName}}</span>
                    <el-select class="search-selector m-r-20" style="width:200px;" clearable v-model="quotaForm.indiModel" placeholder="请选择指标项" v-else>
                        <el-option v-for="item in aQuotaItems" :key="item.id" :label="item.name" :value="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="指标分类" :label-width="formLabelWidth" required>
                    <el-select class="search-selector m-r-20" style="width:200px;" clearable v-model="quotaForm.indiType" placeholder="请选择指标分类">
                        <el-option v-for="item in quotaTypes" :key="item.value" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="指标名称" :label-width="formLabelWidth" required>
                    <el-input v-model="quotaForm.indiName" placeholder="请输入指标名称" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="指标代码" :label-width="formLabelWidth">
                    <span>{{quotaForm.indiCode}}</span>
                </el-form-item>
                <el-form-item label="计算规则" :label-width="formLabelWidth">
                    <el-input type="textarea" v-model="quotaForm.rule" placeholder="请输入计算规则" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="所属机构" :label-width="formLabelWidth" required>
                    <span v-if="quotaForm.id">{{quotaForm.agencyName}}</span>
                    <el-select class="search-selector m-r-20" style="width:200px;" clearable v-model="quotaForm.agency" placeholder="请选择所属机构" v-else>
                        <el-option v-for="item in agencies" :key="item.id" :label="item.name" :value="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="版本" :label-width="formLabelWidth">
                    <el-input v-model="quotaForm.version" placeholder="请输入版本" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="备注" :label-width="formLabelWidth">
                    <el-input v-model="quotaForm.comment" placeholder="请输入备注" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="创建人" :label-width="formLabelWidth">
                    <span>{{quotaForm.creator}}</span>
                </el-form-item>
                <el-form-item label="创建时间" :label-width="formLabelWidth">
                    <span>{{quotaForm.create_time}}</span>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addQuotaVisible = false">取 消</el-button>
                <el-button type="primary" @click="submitQuotaForm">确 定</el-button>
            </div>
        </el-dialog>
        <!-- 添加/编辑 指标 弹窗end -->

        <el-tabs class="m-t-10" type="border-card" v-model="active" @tab-click="clickTab">
            <el-tab-pane label="基础指标结果视图" name="QuotaResultView">
                <el-table
                    ref="resultTable"
                    :data="quotaResultDatas"
                    border
                    class="m-t-10"
                    style="width: 100%"
                    :height="tableHeight"
                    :header-cell-style="tableHeadStyle">
                    <el-table-column prop="id" label="数据日期" header-align="center" align="center">
                        <template slot-scope="scope">
                            <span>{{new Date(scope.row.dataDt) | convertDate}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="indiName" label="指标名称" header-align="center" align="center"></el-table-column>
                    <el-table-column prop="orgCode" label="机构编码" header-align="center" align="center"></el-table-column>
                    <el-table-column prop="orgName" label="机构" header-align="center" align="center"></el-table-column>
                    <el-table-column prop="currCd" label="币种" header-align="center" align="center"></el-table-column>
                    <el-table-column prop="dataValue" label="数值" header-align="center" align="center"></el-table-column>
                </el-table>
                <el-pagination
                    v-if="quotaResultTotal > 0"
                    class="text-right m-t-20"
                    layout="prev, pager, next"
                    :current-page.sync="quotaResultPage"
                    :page-size="pageSize"
                    :total="quotaResultTotal"
                    @current-change="getQuotaResultDatas"
                >
                </el-pagination>
            </el-tab-pane>
            <el-tab-pane label="基础指标调度配置视图" name="QuotaScheduleView">
                <div class="handle-panel bg-gray-lightest p-10  m-t-10">
                    <el-button size="small" icon="el-icon-circle-plus-outline" type="primary" @click="startAddQuotaSchedule">新增</el-button>
                    <el-button size="small" icon="el-icon-edit" @click="startEditQuotaSchedule">修改</el-button>
                    <el-button size="small" icon="el-icon-delete" @click="startDeleteQuotaSchedule">删除</el-button>
                </div>
                <el-table
                    ref="scheduleTable"
                    :data="quotaScheduleDatas"
                    border
                    class="m-t-10"
                    style="width: 100%"
                    :height="tableHeight"
                    :header-cell-style="tableHeadStyle"
                    @selection-change="handleScheduleSelectionChange">
                    <el-table-column type="selection" width="55" align="center"></el-table-column>
                    <el-table-column prop="indiName" label="自定义指标" header-align="center" align="center"></el-table-column>
                    <el-table-column prop="processInst" label="处理指令" header-align="center" align="center"></el-table-column>
                    <el-table-column label="指令类别" header-align="center" align="center">
                        <template slot-scope="scope"><!-- 1.SQL语句 2.存储过程 -->
                            <span>{{scope.row.instType==1?'SQL语句': '存储过程'}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="状态" header-align="center" align="center">
                        <template slot-scope="scope"><!-- 0.有效 1.无效 -->
                            <span>{{scope.row.isValid==0?'有效': '无效'}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="创建人" header-align="center" align="center">
                        <template slot-scope="scope">
                            <span>管理员</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="创建时间" header-align="center" align="center">
                        <template slot-scope="scope">
                            <span>{{new Date(scope.row.createDt) | convertDate('yyyy-MM-dd hh:mm')}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="最后修改人" header-align="center" align="center">
                        <template slot-scope="scope">
                            <span>管理员</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="最后修改时间" header-align="center" align="center">
                        <template slot-scope="scope">
                            <span>{{new Date(scope.row.lastEditDt) | convertDate('yyyy-MM-dd hh:mm')}}</span>
                        </template>
                    </el-table-column>
                </el-table>
                <el-pagination
                    v-if="quotaScheduleTotal > 0"
                    class="text-right m-t-20"
                    layout="prev, pager, next"
                    :current-page.sync="quotaSchedulePage"
                    :page-size="pageSize"
                    :total="quotaScheduleTotal"
                    @current-change="getQuotaScheduleDatas"
                >
                </el-pagination>
            </el-tab-pane>
        </el-tabs>

        <!-- 添加/编辑 指标调度配置 弹窗start -->
        <el-dialog
            :title="addScheduleTitle"
            :visible.sync="addScheduleVisible"
            width="600px"
        >
            <el-form :model="scheduleForm">
                <el-form-item label="处理指令" :label-width="formLabelWidth" required>
                    <el-input v-model="scheduleForm.processInst" placeholder="请输入处理指令" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="指令类别" :label-width="formLabelWidth" required>
                    <el-select class="search-selector m-r-20" style="width:200px;" clearable v-model="scheduleForm.instType" placeholder="请选择指令类别">
                        <el-option v-for="item in aDirectiveTypes" :key="item.id" :label="item.name" :value="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="状态" :label-width="formLabelWidth">
                    <el-radio-group v-model="scheduleForm.isValid">
                        <el-radio :label="0">有效</el-radio>
                        <el-radio :label="1">无效</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="创建人" :label-width="formLabelWidth">
                    <span>{{scheduleForm.creator}}</span>
                </el-form-item>
                <el-form-item label="创建时间" :label-width="formLabelWidth">
                    <span>{{scheduleForm.create_time}}</span>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addScheduleVisible = false">取 消</el-button>
                <el-button type="primary" @click="submitScheduleForm">确 定</el-button>
            </div>
        </el-dialog>
        <!-- 添加/编辑 指标调度配置 弹窗end -->
    </div>
</template>

<script>
    import {mapActions} from 'vuex';
    import Utils from '../../../assets/js/common/utils';

    export default {
        name: 'BasicQuota',
        mixins: [],
        components: {},
        data() {
            return {
                searchForm: {
                    classify1: '',
                    classify2: '',
                    classify3: ''
                },
                aLevel1: [], // 一级指标下拉列表
                aLevel2: [], // 二级指标下拉列表
                aLevel3: [], // 三级指标类别下拉列表
                aaLevel2: [], // 二级指标类别下拉列表（新增/修改弹窗）
                aaLevel3: [], // 三级指标类别下拉列表（新增/修改弹窗）
                aQuotaItems: [], // 指标项下拉选择列表
                agencies: [], // 所属机构
                quotaTypes: [ // 指标分类
                    {value: 1, label: '基础指标'},
                    {value: 2, label: '分析指标'}
                ],
                tableHeadStyle: {
                    'font-weight': 'bold',
                    'color': '#333'
                },
                pageSize: 5,
                quotaDatas: [],
                quotaTotal: 0,
                quotaPage: 1,
                multipleSelection: [], // 勾选的指标
                addQuotaTitle: '',
                addQuotaVisible: false,
                quotaForm: {
                    classify1: '', // 一级指标类别，下拉选择
                    classify1Name: '', // 一级指标类别
                    classify2: '', // 二级指标类别，下拉选择
                    classify2Name: '', // 二级指标类别
                    classify3: '', // 三级指标类别，下拉选择
                    classify3Name: '', // 三级指标类别
                    indiModel: '', // 指标项，下拉选择
                    indiModelName: '', // 指标项
                    indiType: '', // 指标分类，下拉选择（基础指标、分析指标）
                    indiName: '', // 指标名称
                    indiCode: '', // 指标代码（纯展示，后端生成）
                    rule: '', // 计算规则，textarea
                    agency: '', // 所属机构，下拉选择
                    version: '', // 版本
                    comment: '', // 备注
                    creator: '', // 创建人
                    create_time: '' // 创建时间
                },
                formLabelWidth: '120px',
                tableHeight: '100px',
                active: 'QuotaResultView',
                resultQuota: '',
                quotaResultDatas: [],
                quotaResultTotal: 0,
                quotaResultPage: 1,
                quotaScheduleDatas: [],
                quotaScheduleTotal: 0,
                quotaSchedulePage: 1,
                multipleScheduleSelection: [], // 勾选的指标调度配置
                scheduleForm: {
                    processInst: '', // 处理指令
                    instType: '', // 指令类别
                    isValid: '', // 状态 0-有效 1-无效
                    creator: '', // 创建人
                    create_time: '' // 创建时间
                },
                aDirectiveTypes: [
                    {id: 1, name: 'SQL语句'},
                    {id: 2, name: '存储过程'}
                ],
                addScheduleTitle: '',
                addScheduleVisible: false
            }
        },
        beforeRouteEnter(to, from, next) {
            next();
        },
        created() {
            this.getOrgList();
            this.getTypes('aLevel1'); // 设置初始数据（一级指标类别）
            this.getQuotaDatas(); // 获取指标数据
        },
        mounted() {
            this.setHeight();
        },
        computed: {},
        methods: {
            ...mapActions([
                'getCategoryListAction',
                'getQuotaListAction', 'addQuotaAction', 'updateQuotaAction', 'deleteQuotaAction',
                'getOrgListAction',
                'getResultListAction',
                'getScheduleListAction', 'addScheduleAction', 'updateScheduleAction', 'deleteScheduleAction'
            ]),
            /***
             * 获取组织结构
             */
            getOrgList() {
               this.getOrgListAction({}).then(res => {
                   this.agencies = res.list;
               }).catch(e => {
                   this.$message.error(e);
               })
            },
            /***
             * 获取指标类别下拉数据
             * @param arr 要改动的数组
             * @param pid 父级id
             */
            getTypes(arrname, pid) {
                let requestData = {};
                if(pid) requestData.pid = pid;
                this.getCategoryListAction(requestData).then(res => {
                    this[arrname] = res.list;
                }).catch(e => {
                    this.$message.error(e);
                })
            },
            /***
             * 获取指标数据
             */
            getQuotaDatas() {
                let requestData = {
                    type: 0, // 0-基础指标
                    pageSize: this.pageSize,
                    pageNum: this.quotaPage,
                    classify1: this.searchForm.classify1,
                    classify2: this.searchForm.classify2,
                    classify3: this.searchForm.classify3
                };
                let loadingInstance = this.$loading({});
                this.getQuotaListAction(requestData).then(res => {
                    loadingInstance.close();
                    this.quotaDatas = res.list;
                    this.quotaTotal = res.count;
                    // 获取指标结果视图数据
                    if(res.list.length > 0) {
                        this.clickQuota(res.list[0].id);
                    } else {
                        // 结果
                        this.quotaResultDatas = [];
                        this.quotaResultPage = 1;
                        this.quotaResultTotal = 0;
                        // 调度
                        this.quotaScheduleDatas = [];
                        this.quotaSchedulePage = 1;
                        this.quotaScheduleTotal = 0;
                    }
                }).catch(e => {
                    loadingInstance.close();
                    this.$message.error(e);
                })
            },
            /***
             * 设置table高度
             */
            setHeight() {
                setTimeout(()=> {
                    const clientH = window.innerHeight;
                    this.tableHeight = clientH / 3 + 'px';
                }, 200);
            },
            /***
             * 处理指标表格勾选事件
             */
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            /***
             * 指标搜索
             */
            startSearch() {
                this.quotaPage = 1;
                this.getQuotaDatas();
            },
            /***
             * 新增指标：初始化数据 并展示弹窗
             */
            startAddQuota() {
                const now = Utils.convertDate(new Date(), 'yyyy-MM-dd hh:mm:ss');
                this.aaLevel2 = [];
                this.aaLevel3 = [];
                this.quotaForm = {
                    classify1: '', // 一级指标类别，下拉选择
                    classify2: '', // 二级指标类别，下拉选择
                    classify3: '', // 三级指标类别，下拉选择
                    indiModel: '', // 指标项，下拉选择
                    indiType: '', // 指标分类，下拉选择（基础指标、分析指标）
                    indiName: '', // 指标名称
                    indiCode: '', // 指标代码（纯展示，后端生成）
                    rule: '', // 计算规则，textarea
                    agency: '', // 所属机构，下拉选择
                    version: '', // 版本
                    comment: '', // 备注
                    creator: '管理员', // 创建人
                    create_time: now // 创建时间
                };
                this.addQuotaTitle = '新增';
                this.addQuotaVisible= true;
            },
            /***
             * 编辑指标：初始化数据 并展示弹窗
             */
            startEditQuota() {
                const CHECKED_ITEMS = this.multipleSelection;
                if(CHECKED_ITEMS.length !== 1) {
                    // 选择了多个指标，或者没有勾选
                    this.$message.warning('请选择一个指标!');
                    return;
                }
                const now = Utils.convertDate(new Date(), 'yyyy-MM-dd hh:mm:ss');
                this.quotaForm = {
                    id: CHECKED_ITEMS[0].id,
                    classify1Name: CHECKED_ITEMS[0].classify1Name, // 一级指标类别
                    classify2Name: CHECKED_ITEMS[0].classify2Name, // 二级指标类别
                    classify3Name: CHECKED_ITEMS[0].classify3Name, // 三级指标类别
                    indiModelName: CHECKED_ITEMS[0].indiModelName, // 指标项
                    indiType: CHECKED_ITEMS[0].indiType, // 指标分类，下拉选择（基础指标、分析指标）
                    indiName: CHECKED_ITEMS[0].indiName, // 指标名称
                    indiCode: CHECKED_ITEMS[0].indiCode, // 指标代码（纯展示，后端生成）
                    rule: CHECKED_ITEMS[0].rule, // 计算规则，textarea
                    agency: CHECKED_ITEMS[0].orgId, // 所属机构，下拉选择
                    agencyName: CHECKED_ITEMS[0].instName, // 所属机构，下拉选择
                    version: CHECKED_ITEMS[0].version, // 版本
                    comment: CHECKED_ITEMS[0].comment, // 备注
                    creator: '管理员', // 创建人
                    create_time: Utils.convertDate(new Date(CHECKED_ITEMS[0].createDt), 'yyyy-MM-dd hh:mm:ss') // 创建时间
                };
                this.addQuotaTitle = '编辑';
                this.addQuotaVisible= true;
            },
            /***
             * 指标表单：校验
             */
            checkQuotaForm() {
                if(!this.quotaForm.id && this.quotaForm.classify1 === '') {
                    this.$message.warning('请选择一级指标类别!');
                    return false;
                }
                if(!this.quotaForm.id && this.quotaForm.classify2 === '') {
                    this.$message.warning('请选择二级指标类别!');
                    return false;
                }
                if(!this.quotaForm.id && this.quotaForm.classify3 === '') {
                    this.$message.warning('请选择三级指标类别!');
                    return false;
                }
                if(!this.quotaForm.id && this.quotaForm.indiModel === '') {
                    this.$message.warning('请选择指标项!');
                    return false;
                }
                if(this.quotaForm.indiType === '') {
                    this.$message.warning('请选择指标分类!');
                    return false;
                }
                if(this.quotaForm.indiName.trim() === '') {
                    this.$message.warning('请输入指标名称!');
                    return false;
                }
                if(!this.quotaForm.id && this.quotaForm.agency === '') {
                    this.$message.warning('请选择所属机构!');
                    return false;
                }
                return true;
            },
            /***
             * 指标表单：点击提交
             */
            submitQuotaForm() {
                if(!this.checkQuotaForm()) return;
                if(this.quotaForm.id) {
                    this.editQuota();
                } else {
                    this.addQuota();
                }
            },
            /***
             * 新增指标：调用接口
             */
            addQuota() {
                let requestData = {
                    type: 0,
                    classify1: this.quotaForm.classify1,
                    classify2: this.quotaForm.classify2,
                    classify3: this.quotaForm.classify3,
                    indiModel: this.quotaForm.indiModel,
                    indiType: this.quotaForm.indiType,
                    indiName: this.quotaForm.indiName,
                    orgId: this.quotaForm.agency,
                    rule: this.quotaForm.rule,
                    comment: this.quotaForm.comment,
                    version: this.quotaForm.version
                };
                this.addQuotaAction(requestData).then(res => {
                    this.$message.success('新增成功!');
                    this.addQuotaVisible = false;
                    this.getQuotaDatas();
                }).catch(e => {
                    this.$message.error(e);
                })
            },
            /***
             * 编辑指标：调用接口
             */
            editQuota() {
                let requestData = {
                    id: this.quotaForm.id,
                    indiType: this.quotaForm.indiType,
                    indiName: this.quotaForm.indiName,
                    rule: this.quotaForm.rule,
                    comment: this.quotaForm.comment,
                    version: this.quotaForm.version
                };
                this.updateQuotaAction(requestData).then(res => {
                    this.$message.success('修改成功!');
                    this.addQuotaVisible = false;
                    this.getQuotaDatas();
                }).catch(e => {
                    this.$message.error(e);
                })
            },
            /***
             * 删除指标
             */
            startDeleteQuota() {
                const CHECKED_ITEMS = this.multipleSelection;
                if(CHECKED_ITEMS.length === 0) {
                    this.$message.warning('请选择要删除的指标!');
                    return;
                }
                this.$confirm('确认删除选中的指标？', '提示', {
                    confirmButtonText: '确定',
                    showCancelButton: false,
                    type: 'warning'
                }).then(() => {
                    this.deleteQuota();
                }).catch((action) => {
                    console.log(action);
                })
            },
            /***
             * 删除指标：调用接口
             */
            deleteQuota() {
                const CHECKED_ITEMS = this.multipleSelection;
                this.deleteQuotaAction({id: CHECKED_ITEMS[0].id}).then(res => {
                    this.$message.success('删除成功!');
                    this.getQuotaDatas();
                }).catch(e => {
                    this.$message.error(e);
                })
            },
            /***
             * 点击指标名称，获取结果视图和配置视图数据
             */
            clickQuota(id) {
                this.resultQuota = id;
                this.quotaResultPage = 1;
                this.getQuotaResultDatas();
                this.getQuotaScheduleDatas();
            },
            /***
             * tabs['基础指标结果视图', '基础指标调度配置视图']
             * 点击tab回调处理
             * @param tab
             */
            clickTab(tab) {},
            /***
             * 获取指标结果视图数据
             */
            getQuotaResultDatas() {
                this.getResultListAction({
                    indiId: this.resultQuota,
                    pageNum: this.quotaResultPage,
                    pageSize: this.pageSize
                }).then(res => {
                    this.quotaResultDatas = res.list;
                    this.quotaResultTotal = res.count;
                }).catch(e => {
                    this.$message.error(e);
                })
            },
            /***
             * 获取指标调度配置视图数据
             */
            getQuotaScheduleDatas() {
                this.getScheduleListAction({
                    indiId: this.resultQuota
                }).then(res => {
                    this.quotaScheduleDatas = res.list;
                    this.quotaScheduleTotal = res.count;
                }).catch(e => {
                    this.$message.error(e);
                })
            },
            /***
             * 处理指标调度配置table勾选事件
             */
            handleScheduleSelectionChange(val) {
                this.multipleScheduleSelection = val;
            },
            /***
             * 新增指标调度配置：初始化数据 并展示弹窗
             */
            startAddQuotaSchedule() {
                const now = Utils.convertDate(new Date(), 'yyyy-MM-dd hh:mm:ss');
                this.scheduleForm = {
                    processInst: '', // 处理指令
                    instType: '', // 指令类别
                    isValid: '', // 状态 0-有效 1-无效
                    creator: '管理员', // 创建人
                    create_time: now // 创建时间
                };
                this.addScheduleTitle = '新增';
                this.addScheduleVisible= true;
            },
            /***
             * 编辑指标调度配置：初始化数据 并展示弹窗
             */
            startEditQuotaSchedule() {
                const CHECKED_ITEMS = this.multipleScheduleSelection;
                if(CHECKED_ITEMS.length !== 1) {
                    // 选择了多个，或者没有勾选
                    this.$message.warning('请选择一个配置!');
                    return;
                }
                const now = Utils.convertDate(new Date(), 'yyyy-MM-dd hh:mm:ss');
                this.scheduleForm = {
                    id: CHECKED_ITEMS[0].id,
                    processInst: CHECKED_ITEMS[0].processInst, // 处理指令
                    instType: CHECKED_ITEMS[0].instType, // 指令类别
                    isValid: CHECKED_ITEMS[0].isValid, // 状态
                    creator: '管理员', // 创建人
                    create_time: now // 创建时间
                };
                this.addScheduleTitle = '编辑';
                this.addScheduleVisible= true;
            },
            /**
             * 指标调度配置表单：校验
             */
            checkScheduleForm() {
                if(this.scheduleForm.processInst.trim() === '') {
                    this.$message.warning('请输入处理指令!');
                    return false;
                }
                if(this.scheduleForm.instType === '') {
                    this.$message.warning('请选择指令类别!');
                    return false;
                }
                if(this.scheduleForm.isValid === '') {
                    this.$message.warning('请选择状态!');
                    return false;
                }
                return true;
            },
            /***
             * 指标调度配置表单：点击提交
             */
            submitScheduleForm() {
                if(!this.checkScheduleForm()) return;
                if(this.scheduleForm.id) {
                    this.editSchedule();
                } else {
                    this.addSchedule();
                }
            },
            /***
             * 指标调度配置 新增
             */
            addSchedule() {
                this.addScheduleAction({
                    indiId: this.resultQuota,
                    processInst: this.scheduleForm.processInst,
                    instType: this.scheduleForm.instType,
                    isValid: this.scheduleForm.isValid
                }).then(res => {
                    this.$message.success('新增成功！');
                    this.addScheduleVisible= false;
                    this.getQuotaScheduleDatas();
                }).catch(e => {
                    this.$message.error(e);
                })
            },
            /***
             * 指标调度配置 修改
             */
            editSchedule() {
                this.updateScheduleAction({
                    id: this.scheduleForm.id,
                    indiId: this.resultQuota,
                    processInst: this.scheduleForm.processInst,
                    instType: this.scheduleForm.instType,
                    isValid: this.scheduleForm.isValid
                }).then(res => {
                    this.$message.success('修改成功！');
                    this.addScheduleVisible= false;
                    this.getQuotaScheduleDatas();
                }).catch(e => {
                    this.$message.error(e);
                })
            },
            /***
             * 删除指标调度配置
             */
            startDeleteQuotaSchedule() {
                const CHECKED_ITEMS = this.multipleScheduleSelection;
                if(CHECKED_ITEMS.length === 0) {
                    this.$message.warning('请选择要删除的配置!');
                    return;
                }
                this.$confirm('确认删除选中的配置？', '提示', {
                    confirmButtonText: '确定',
                    showCancelButton: false,
                    type: 'warning'
                }).then(() => {
                    this.deleteQuotaSchedule();
                }).catch((action) => {
                    console.log(action);
                })
            },
            /***
             * 删除指标调度配置：调用接口
             */
            deleteQuotaSchedule() {
                const CHECKED_ITEMS = this.multipleScheduleSelection;
                this.deleteScheduleAction({
                    id: CHECKED_ITEMS[0].id
                }).then(res => {
                    this.$message.success('删除成功！');
                    this.getQuotaScheduleDatas();
                }).catch(e => {
                    this.$message.error(e);
                })
            }
        },
        filters: {},
        watch: {
            'searchForm.classify1'(val) {
                this.searchForm.classify2 = '';
                this.searchForm.classify3 = '';
                this.aLevel3 = [];
                if(val !== '') {
                    this.getTypes('aLevel2', this.searchForm.classify1);
                } else {
                    this.aLevel2 = [];
                }
            },
            'searchForm.classify2'(val) {
                this.searchForm.classify3 = '';
                if(val !== '') {
                    this.getTypes('aLevel3', this.searchForm.classify2);
                } else {
                    this.aLevel3 = [];
                }
            },
            'quotaForm.classify1'(val) {
                this.quotaForm.classify2 = '';
                this.quotaForm.classify3 = '';
                this.aaLevel3 = [];
                if(val !== '') {
                    this.getTypes('aaLevel2', this.quotaForm.classify1);
                } else {
                    this.aaLevel2 = [];
                }
            },
            'quotaForm.classify2'(val) {
                this.quotaForm.classify3 = '';
                this.quotaForm.indiModel = '';
                this.aQuotaItems = [];
                if(val !== '') {
                    this.getTypes('aaLevel3', this.quotaForm.classify2);
                } else {
                    this.aaLevel3 = [];
                }
            },
            'quotaForm.classify3'(val) {
                this.quotaForm.indiModel = '';
                if(val !== '') {
                    this.getTypes('aQuotaItems', this.quotaForm.classify3);
                } else {
                    this.aQuotaItems = [];
                }
            }
        },
        destroyed() {
        },
        beforeRouteLeave(to, from, next) {
            next();
        }
    }
</script>

<style lang="scss" scoped>

</style>



